.floating-icons-container {
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 100vw;
  height: 75vh;
}

.black-icon {
  position: absolute;
  top: 20%;
  left: 0%;
  margin-left: -10%;
  width: 25%;
  z-index: 0;
}

.yellow-icon {
  position: absolute;
  width: 25%;
  bottom: 0;
  right: 0;
  margin-right: -10%;
  overflow: hidden;
  z-index: 1;
}
@media screen and (max-width: 768px) {
  .black-icon {
    width: 20%;
  }
  .yellow-icon {
    margin-bottom: 50%;
  }
}
